import { useState, useEffect } from "react";
import Loading from "./component/Loading";
import HomePage from "./HomePage";
import Login from "./Login";
import "./App.css";
import ProductItem from "./ProductItem";
import "bootstrap/dist/css/bootstrap.min.css";
import { Container } from "react-bootstrap";
import { Route, Routes } from "react-router-dom";
import ProductSection from "./ProductSection.js";
import Cart from "./Cart";
import NavbarSection from "./NavbarSection";
import OrderHistory from "./page/OrderHistory";
import About from "./page/About";
function App() {
  const [cart, setCart] = useState([]);
  console.log(cart);
  const handleCart = (product) => {
    if (cart.indexOf(product) === -1) {
      setCart([...cart, product]);
    }
  };
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    setTimeout(() => {
      setLoading(false);
    }, 500);
  });
  return (
    <>
      <div className="App">
        {loading === true ? (
          <Loading />
        ) : (
          <>
            <Container>
              <Routes>
                <Route index element={<Login />} />
                <Route path="/home" element={<HomePage />} />
                <Route
                  path="/product/:id"
                  element={<ProductItem handleCart={handleCart} />}
                />
                <Route path="/about" element={<About />} />
                <Route
                  path="/product"
                  element={
                    <ProductSection cart={cart} handleCart={handleCart} />
                  }
                />
                <Route
                  path="/cart"
                  element={<Cart cart={cart} setCart={setCart} />}
                />
                <Route
                  path="/orderHistory"
                  element={<OrderHistory cart={cart} />}
                />
                <Route path="/nav" element={<NavbarSection cart={cart} />} />
              </Routes>
            </Container>
          </>
        )}
      </div>
    </>
  );
}

export default App;
