import { Discount } from "@mui/icons-material";
import { Typography, Button, FormGroup } from "@mui/material";
import React from "react";
import { useEffect } from "react";
import { useState } from "react";
import { Container, Col, Row, Image, Form } from "react-bootstrap";
import { Link } from "react-router-dom";
import NavbarSection from "./NavbarSection";

const Cart = ({ cart, setCart }) => {
  const [validated, setValidated] = useState(false);
  const [shipping, setShipping] = useState(8);
  const [taxes, setTaxes] = useState(625 / 100);
  const [discount, setDiscount] = useState(6.25 / 100);
  const handleRemove = (id) => {
    const newCart = cart.filter((product) => product.id !== id);
    setCart(newCart);
    handlePrice();
  };

  const handleSubmit = (event) => {
    const form = event.currentTarget;
    if (form.checkValidity() === false) {
      event.preventDefault();
      event.stopPropagation();
    }

    setValidated(true);
  };
  const handleChange = (item, d) => {
    const ind = cart.indexOf(item);
    const newProduct = cart;
    newProduct[ind].quantity += d;

    if (newProduct[ind].quantity === 0) newProduct[ind].quantity = 1;
    setCart([...newProduct]);
  };

  const [total, setTotal] = useState(0);
  const checkout = () => {
    alert(
      `Thanks for shopping with us , total : $${total} , your payment was successful`
    );
    setCart([]);
  };

  const handlePrice = () => {
    let newTotal = 0;
    let newTaxes = 0;
    let newDiscount = 0;
    cart.map((product) => {
      newTaxes += (product.price * product.quantity * taxes) / 100;
      newTotal += product.price * product.quantity + newTaxes + shipping;
      return newTotal;
    });
    setTotal(Math.round(newTotal));
  };

  useEffect(() => {
    handlePrice();
  });
  return (
    <Container className="my-5">
      <Row>
        <Col sm={12} md={8} lg={8}>
          {cart.map((product, index) => {
            return (
              <div key={index} className="item-payment">
                <Image
                  className="rounded-4 mb-5"
                  width={200}
                  src={product.image}
                ></Image>
                <div className="cart-item-product">
                  <div className="cart-item-detail">
                    <div className="cart-item-product-name">
                      <Typography variant="h5">{product.name}</Typography>
                      <Typography variant="h6">
                        ${product.price * product.quantity}
                      </Typography>
                    </div>
                    <button
                      onClick={() => handleRemove(product.id)}
                      className="remove-btn"
                    >
                      Remove
                    </button>
                  </div>
                  <div className="button">
                    <button onClick={() => handleChange(product, -1)}>-</button>
                    <div>{product.quantity}</div>
                    <button onClick={() => handleChange(product, 1)}>+</button>
                  </div>
                </div>
              </div>
            );
          })}

          <Typography variant="h6">Discount code</Typography>
          <Row>
            <Col xs={10}>
              <Form.Control type="text"></Form.Control>
            </Col>
            <Col>
              <Col xs={2}>
                <Button>Apply</Button>
              </Col>
            </Col>
          </Row>

          <Row>
            <div className="border-bottom">
              <Typography>Shipping</Typography>
              <Typography>${shipping}</Typography>
            </div>
            <div className="border-bottom">
              <Typography>Discount</Typography>
            </div>
            <div className="border-bottom">
              <Typography>Taxes</Typography>
              <Typography>{taxes}%</Typography>
            </div>
            <div className="border-bottom">
              <Typography>Total</Typography>
              <Typography>${total}</Typography>
            </div>
            <Link to="/product">
              <button className="add-to-cart">See more product</button>
            </Link>
          </Row>
        </Col>
        <Col sm={12} md={4} lg={4} className="my-2">
          <Form onSubmit={handleSubmit} noValidate validated={validated}>
            <Typography variant="h5">Contact Information</Typography>
            <Form.Group className="mb-3">
              <Form.Label>Email address</Form.Label>
              <Form.Control minLength={5} required type="email"></Form.Control>
              <Form.Control.Feedback>Looks Good!</Form.Control.Feedback>
            </Form.Group>
            <Typography variant="h5">Payment details</Typography>
            <Form.Group className="mb-3">
              <Form.Label>Name on card</Form.Label>
              <Form.Control required type="text"></Form.Control>
            </Form.Group>
            <Form.Group className="mb-3">
              <Form.Label>Card number</Form.Label>
              <Form.Control required type="number"></Form.Control>
            </Form.Group>
            <Row>
              <Col xs={8}>
                <Form.Label>Expired date(MM/YY)</Form.Label>
                <Form.Control required type="date"></Form.Control>
              </Col>
              <Col>
                <Form.Label>CVC</Form.Label>
                <Form.Control required maxLength={4} type="tel"></Form.Control>
              </Col>
            </Row>
            <Form.Group className="my-3">
              <Form.Label>Apartment , suite , etc</Form.Label>
              <Form.Control required type="text"></Form.Control>
            </Form.Group>
            <Row>
              <Col>
                <Form.Label>City</Form.Label>
                <Form.Control required type="text"></Form.Control>
              </Col>
              <Col>
                <Form.Label>State/Province</Form.Label>
                <Form.Control required type="text"></Form.Control>
              </Col>
              <Col>
                <Form.Label>Postal code</Form.Label>
                <Form.Control required type="number"></Form.Control>
              </Col>
            </Row>
            <Form.Group className="my-3" controlId="formBasicCheckbox">
              <Form.Check
                required
                type="checkbox"
                label="Billing address is the same as shipping address"
              />
            </Form.Group>
            {
              <button onClick={checkout} type="submit" className="add-to-cart">
                Pay ${total}
              </button>
            }
          </Form>

          <Typography>Payment details stored in plain text</Typography>
        </Col>
      </Row>
    </Container>
  );
};

export default Cart;
