import React from "react";
import { Container } from "react-bootstrap";
import { Badge } from "@mui/material";
import { useState } from "react";
import ShoppingCartIcon from "@mui/icons-material/ShoppingCart";
import { Nav, Navbar, NavDropdown } from "react-bootstrap";
const NavbarSection = ({ cart }) => {
  return (
    <Navbar bg="light" expand="lg">
      <Navbar.Toggle aria-controls="basic-navbar-nav" />
      <Navbar.Brand href="#home">EDERSEN</Navbar.Brand>
      <Nav.Link href="#/cart">
        <Badge badgeContent={cart.length} color="primary">
          <ShoppingCartIcon className="ShoppingCartIcon" />
        </Badge>
      </Nav.Link>
      <Navbar.Collapse id="basic-navbar-nav">
        <Nav className="me-auto">
          <Nav.Link href="#/product">Product</Nav.Link>
          <Nav.Link href="#link">Learn</Nav.Link>
          <NavDropdown title="Shop" id="basic-nav-dropdown">
            <NavDropdown.Item href="#action/3.1">Shop 1</NavDropdown.Item>
            <NavDropdown.Item href="#action/3.2">shop 2</NavDropdown.Item>
            <NavDropdown.Item href="#action/3.3">shop 3</NavDropdown.Item>
          </NavDropdown>
        </Nav>
        <Nav>
          <Nav.Link href="#/about">About</Nav.Link>
          <Nav.Link href="#/orderHistory">History</Nav.Link>
          <Nav.Link eventKey={2} href="#/">
            Login
          </Nav.Link>
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  );
};

export default NavbarSection;
