import React from "react";
import { Link, useParams } from "react-router-dom";
import { Container, Image, Col, Row, Button } from "react-bootstrap";
import StarIcon from "@mui/icons-material/Star";
import { Rating, Slider } from "@mui/material";
import Review from "./page/Review";
import ChevronLeftIcon from "@mui/icons-material/ChevronLeft";
import { useState } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faHeart } from "@fortawesome/free-regular-svg-icons";
import bestProductLaptop from "./data/bestProductLaptop";
import { Typography } from "@mui/material";
import { Add } from "@mui/icons-material";
const ProductItem = () => {
  const [cart, setCart] = useState([]);
  const handleCart = (product) => {
    if (cart.indexOf(product) === -1) {
      setCart([...cart, product]);
    }
  };

  const [value, setValue] = React.useState(2);
  const [hover, setHover] = React.useState(-1);
  const labels = {
    0.5: "Useless",
    1: "Useless+",
    1.5: "Poor",
    2: "Poor+",
    2.5: "Ok",
    3: "Ok+",
    3.5: "Good",
    4: "Good+",
    4.5: "Excellent",
    5: "Excellent+",
  };
  function getLabelText(value) {
    return `${value} Star${value !== 1 ? "s" : ""}, ${labels[value]}`;
  }

  const { id } = useParams();
  return (
    <Container>
      {bestProductLaptop
        .filter((product) => product.id === id)
        .map((product, index) => (
          <div key={index}>
            <Row>
              <Col
                style={{
                  position: "relative",
                }}
                sm={12}
                md={8}
                lg={6}
              >
                <Link to="/product">
                  <ChevronLeftIcon
                    style={{
                      position: "absolute",
                      top: -10,
                      left: 10,
                      fontSize: 100,
                    }}
                  />
                  <Image
                    className="img-fluid rounded-5"
                    src={product.image}
                  ></Image>
                </Link>{" "}
                <Review className="mt-5" />
              </Col>

              <Col sm={12} md={4} lg={6} className="product-detail">
                <div>
                  <Typography variant="h4">{product.name}</Typography>
                  <Typography variant="h5">${product.price}</Typography>

                  <Rating
                    name="simple-controlled"
                    value={4}
                    onChange={(event, newValue) => {
                      setValue(newValue);
                    }}
                  />
                  <Typography>{product.description}</Typography>
                  <div className="color-section my-4">
                    <button className="color-box black "></button>
                    <button className="color-box white"></button>
                    <button className="color-box gray"></button>
                  </div>
                  <div className="heart-button">
                    <button
                      onClick={() => handleCart(product)}
                      className="add-to-cart"
                    >
                      Add to Cart
                    </button>
                    <FontAwesomeIcon
                      className="hover:text-red-500"
                      icon={faHeart}
                    />
                  </div>
                  <div className="featured-product">
                    <Typography variant="h5" color="blue">
                      Featured
                    </Typography>
                    <div>{product.features}</div>
                  </div>
                  <div className="plus">
                    <Typography>Core</Typography>
                    <Add />
                  </div>
                  <div className="plus">
                    <Typography>Shipping</Typography>
                    <Add />
                  </div>
                  <div className="plus">
                    <Typography>Return</Typography>
                    <Add />
                  </div>
                </div>
              </Col>
            </Row>
          </div>
        ))}
    </Container>
  );
};

export default ProductItem;
