import { Typography } from "@mui/material";
import React from "react";

const Circle = ({ color, number, detail }) => {
  const circleStyle = {
    width: 100,
    height: 100,
    borderRadius: "50%",
    backgroundColor: color,
    position: "relative",
  };
  const position = {
    position: "absolute",
    bottom: -10,
    right: -30,
  };
  const positionDetail = {
    position: "absolute",
    bottom: -30,
    right: -30,
  };

  return (
    <div style={circleStyle}>
      <Typography variant="h2" style={position}>
        {number}
      </Typography>
      <Typography style={positionDetail}>{detail}</Typography>
    </div>
  );
};

export default Circle;
