import React from "react";
import { Typography, Rating } from "@mui/material";
import Progress from "../component/Progress";
import comment from "../data/comment";
import AddReview from "../component/AddReview";
import { nanoid } from "nanoid";
import { useState } from "react";
import DeleteIcon from "@mui/icons-material/Delete";

const Review = () => {
  const [reviewComment, setReviewComment] = useState(comment);
  console.log(reviewComment);
  function handleReview(text) {
    const date = new Date().toLocaleDateString();
    const newNote = {
      id: nanoid(),
      name: "Kevin",
      body: text,
      date: date,
    };
    const newNotes = [...reviewComment, newNote];
    setReviewComment(newNotes);
  }
  const deleteReview = (id) => {
    const newReview = reviewComment.filter(
      (curComment) => curComment.id !== id
    );
    setReviewComment(newReview);
  };

  return (
    <div>
      <Typography variant="h5" className="mt-5">
        Customer Reviews
      </Typography>
      <div className="rating">
        <Rating defaultValue={4.5} readonly precision={0.5} />
        <Typography>Based on 1624 Reviews</Typography>
      </div>
      <div>
        <Progress done={63} star={5} />
        <Progress done={10} star={4} />
        <Progress done={6} star={3} />
        <Progress done={12} star={2} />
        <Progress done={9} star={1} />
      </div>
      <Typography variant="h6" className="mt-4">
        Share your thoughts
      </Typography>
      <Typography>
        If you're used this product , share your thoughts with other customers
      </Typography>
      <button className="review mt-4">Write a review</button>
      <AddReview className="my-3" handleReview={handleReview} />
      <div className="comment my-3">
        {reviewComment.map((curComment, value) => {
          return (
            <div key={value} className="review-detail mb-5">
              <div className="review-name">
                <Typography style={{ fontWeight: "700" }}>
                  {curComment.name}
                </Typography>
                <Typography>{curComment.date}</Typography>
                <Rating precision={0.5} defaultValue={curComment.rating} />
                <Typography>{curComment.body}</Typography>
              </div>
              <DeleteIcon onClick={() => deleteReview(curComment.id)} />
            </div>
          );
        })}
      </div>
    </div>
  );
};

export default Review;
