import { Typography, Button } from "@mui/material";
import { nanoid } from "nanoid";
import React from "react";
import { useState } from "react";
import { useParams } from "react-router-dom";
import { Col, Container, Image, Row } from "react-bootstrap";
const OrderHistory = ({ cart }) => {
  const [history, setHistory] = useState([]);

  const { id } = useParams();
  const addHistory = () => {
    cart.map((product) => {
      const date = new Date().toLocaleDateString();
      const newHistory = {
        id: product.id,
        name: product.name,
        date: date,
        quantity: product.quantity,
        views: product.views,
        reviews: product.reviews,
        image: product.image,
        price: product.price,
        description: product.description,
        category: product.category,
      };
      return addHistory;
    });
    setHistory(addHistory);
  };
  return (
    <Container>
      <Typography variant="h5" className="my-3">
        Order history
      </Typography>
      <small className="text-muted">
        Check the status of recent orders , and download invoices
      </small>
      {cart.map((product, index) => {
        return (
          <div key={index} className="mb-5">
            <Row>
              <Col
                sm={4}
                md={4}
                className="d-flex flex-column justify-content-between"
              >
                <Typography>Order name</Typography>
                <Typography>{product.id}</Typography>
              </Col>
              <Col
                sm={4}
                md={4}
                className="d-flex  flex-column align-items-center justify-content-between"
              >
                <Typography>Placed Date</Typography>
                <Typography>{product.quantity}</Typography>
              </Col>
              <Col
                sm={4}
                md={4}
                className="d-flex  flex-column align-items-center justify-content-between"
              >
                <Typography>Total amount</Typography>
                <Typography>{product.quantity}</Typography>
              </Col>
            </Row>
            <div className="d-flex  justify-content-between align-items-center">
              <Image
                className="img-fluid rounded-3"
                width={300}
                src={product.image}
              ></Image>
              <div className="mx-5">
                <Typography variant="h5">{product.name}</Typography>
                <Typography>{product.description}</Typography>
                <a href={`#/product/${product.id}`}>
                  <Button variant="outlined">View Product</Button>
                </a>

                <a href="#/product">
                  <Button variant="outlined">Buy Again</Button>
                </a>

                <Typography>Deliver on January 25 , 2023</Typography>
              </div>
              <Typography>${product.price}</Typography>
            </div>
          </div>
        );
      })}
    </Container>
  );
};

export default OrderHistory;
